body {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    font-family: 'Roboto', sans-serif;
    background: #EAEAEA;
}

#root {
    display: flex;
