}

.p-breadcrumb {
    margin-bottom: -20px
}

.p-breadcrumb ul li .p-menuitem-link .p-menuitem-text {
    font-size: 11px;
}

.p-breadcrumb ul li .p-menuitem-link .p-menuitem-icon,
