}

.container-fluid h4 {
    color: #0096C5;
    color: var(--titulo-secao);
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 0px;
    font-weight: 600;
}

.p-field, label.checkbox {
    color: #3F4745;
