}

.p-checkbox {
    margin-bottom: 20px;
    vertical-align: top;
}

.container-fluid .p-button {
    background: #0096C5;
    background: var(--cor-principal);
    border: 1px solid #0096C5;
    border: 1px solid var(--cor-principal);
    border-radius: 30px;
    width: 150px;
    height: 40px;
	padding: 7.5px 38px;
	font-weight: 600;
}

.container-fluid .p-button .p-button-secondary  {
	background: #FFFFFF;
    border: 3px solid #0096C5;
    border: 3px solid var(--cor-principal);
    border-radius: 30px;
}

.container-fluid .p-button .p-button-secondary span {
	color: #002E3E;
	color: var(--cor-principal-dark);
    font-weight: 600;
}

.container-fluid .p-button.p-button-icon {
    border-radius: 20px;
    padding: 0;
    margin: 0 5px;
    background-color: transparent;
    border: none;
    width: auto;
}

.p-column-title {
    flex: 1 1;
    font-size: 14px;
    color: #33495c;
}

.p-datatable .p-datatable-thead > tr > th {
    border-top: 0;
    padding-bottom: 5px;
    color: var(--cor-texto);
    font-weight: bold;
}

img.icon {
    height: 28px;
    width: 28px;
}

.p-button.p-button-secondary {
    background: #FFFFFF;
    background: var(--botao-neutro);
    border: #0096C5 1px solid;
    border: var(--borda-botao-neutro) 1px solid;
    color: #0096C5;
    color: var(--borda-botao-neutro);
}

.p-button.p-button-warn {
    background: #0096C5;
    background: var(--botao-alerta);
    border: #0096C5 1px solid;
    border: var(--botao-alerta) 1px solid;
}

.p-tooltip {
    pointer-events: none;
}

.p-button-login {
    background: #0096C5;
    background: var(--cor-principal);
    border: 1px solid #DCDCDC;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
