.p-icon-28 {
    font-size: 28px;
    color: #002E3E;
    color: var(--cor-principal-dark);
}

.p-fluid .p-inputtext {
    font-weight: 500;
    font-size: 14px;
    display: flex;
    align-items: center;
    -webkit-font-feature-settings: 'pnum' on, 'lnum' on;
            font-feature-settings: 'pnum' on, 'lnum' on;
    color: #3F4745;
