}

.p-bg-light {
    background: #f9f9f9;
    border-radius: 3px;
    padding: 1rem;
}

.p-divider {
    margin-bottom: 3rem;
}

.title {
    font-family: 'Roboto', medium;
    font-size: 22px;
    font-weight: 600;
}

.p-invalid {
    border: 1px solid red;
    border-radius: 5px;
}

.p-button-logout {
    border-radius: 30px;
